'use client'
import { useState, useEffect, useContext } from "react"
import { CarrinhoContext } from "@/app/carrinho/CarrinhoContext"
import styles from "./Post.module.css"


type Pizza = {
    id: number,
    nome: string,
    descricao: string,
    preco: number,
    imagem: string
}

export default function Post() {
    const carrinhoContext = useContext(CarrinhoContext);

    if (!carrinhoContext) {
        throw new Error("CarrinhoContext is undefined");
    }

    const { adicionarPizza } = carrinhoContext;

    const [pizzas, setPizzas] = useState<Pizza[]>([]);
    const [carrinho, setCarrinho] = useState<Pizza[]>([]);


    useEffect(() => {
        fetch("http://localhost:3000/cardapio")
            .then((res) => res.json())
            .then((data) => setPizzas(data));
    }, [])

    useEffect(() => {
        console.log(carrinho);
    }, [carrinho]);

    return (
        <>
            <section className={styles.post}>
                {pizzas.map((pizza) => (
                    <div key={pizza.id} className={styles.postPizza}>
                        <img src={`http://localhost:3000${pizza.imagem}`} alt="" />
                        <h1>{pizza.nome}</h1>
                        <p>{pizza.descricao}</p>
                        <h2>{pizza.preco}</h2>
                        <button type="submit" onClick={() => adicionarPizza(pizza)}>
                            <h1>Pedir agora</h1></button>
                    </div>
                ))}
            </section>
        </>
    )
}